* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Spectral", serif;
  overflow-x: hidden;
}

.card-link {
  color: inherit;
  cursor: pointer;
}

.section-header {
  background-color: black;
  height: 30vh;
  padding: 1rem;
}
.section-header__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
}
.section-header .h1 {
  font-size: 4rem;
  margin-top: 5rem;
  padding-right: 10%;
  text-decoration: none;
  color: #ffffff;
}
.section-header .nav-link {
  padding-bottom: 0;
}
.section-header .section-header__user {
  color: white;
  text-decoration: none;
}
.section-header .section-header__user svg {
  margin-left: 0.5rem;
}

@media (max-width: 450px) {
  .section-header {
    height: 15vh;
    padding-bottom: 0;
  }
  .section-header__wrapper {
    flex-wrap: wrap;
  }
  .section-header .h1 {
    font-size: 2rem;
    margin-top: 0.5rem;
  }
  .section-header .navbar {
    order: 3;
  }
  .section-header .navbar-nav .dropdown-menu {
    position: absolute;
  }
  .section-header .dropdown-menu[data-bs-popper] {
    left: -50%;
  }
  .section-header .navbar-nav {
    display: flex;
    flex-direction: row;
    gap: 30%;
  }
}
.section__OnTop {
  padding: 3rem;
  display: flex;
  flex-direction: row;
  gap: 3%;
  height: fit-content;
}
.section__OnTop .OnTop__card, .section__OnTop .OnTop__aside {
  display: flex;
  flex-direction: column;
}
.section__OnTop .OnTop__card {
  gap: 2%;
  width: 60%;
}
.section__OnTop .OnTop__card .OnTop-card-img {
  border: 1px solid black;
  width: 100%;
  height: 450px;
  margin-bottom: 5%;
}
.section__OnTop .OnTop__aside {
  gap: 2%;
}
.section__OnTop .aside-card__card-title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.section__OnTop .OnTop__aside-card {
  display: flex;
  flex-direction: row;
  gap: 2%;
  height: 100%;
}
.section__OnTop .OnTop__aside-card .aside-card__card-img {
  width: 100%;
  height: 80%;
  border: 1px solid black;
}
.section__OnTop .OnTop__aside-card .aside-card__description {
  display: flex;
  flex-direction: column;
  gap: 2%;
}
.section__OnTop .OnTop__aside-card h2 {
  font-size: revert;
  margin-bottom: 0;
}

.section__BestVue {
  padding: 3rem;
  display: flex;
  flex-direction: column;
  gap: 3%;
}
.section__BestVue .BestVue-row {
  display: flex;
  flex-direction: row;
  gap: 3%;
}
.section__BestVue .BestVue-row .BestVue__aside-card {
  display: flex;
  flex-direction: row;
  gap: 3%;
}
.section__BestVue .BestVue-row .BestVue__aside-card .aside-card__card-img {
  border: 1px solid black;
  width: 100%;
  height: 500px;
}
.section__BestVue .BestVue-row .BestVue__aside-card .aside-card__description {
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.section__BestVue .BestVue-row .BestVue__aside-card .aside-card__description .aside-card__card-title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.section__BestVue .BestVue-row .BestVue__card {
  display: flex;
  flex-direction: column;
  gap: 3%;
}
.section__BestVue .BestVue-row .BestVue__card .card__card-img {
  border: 1px solid black;
  width: 100%;
  height: 100%;
}

